import React, { Component } from 'react'
import DialogWindow from './ui/DialogWindow'
import cheers from '../images/cheers.png'
import Paragraph from './ui/Paragraph'
import SubmitBtn from './ui/SubmitBtn'
import { connect } from 'react-redux'
import triggerModal from '../action-creators/pageReducer/triggerModal'
import resetRoom from '../action-creators/eventsReducer/resetRoom'
import { TRIGGER_SUCCESS_MODAL } from '../consts/pageReducer'
import PropTypes from 'prop-types'
import resetValues from '../action-creators/eventsReducer/resetValues'
import { injectIntl, intlShape } from 'react-intl'
import { timeShape, dateShape } from '../intl/shapes'

class EventCreated extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    date: dateShape.isRequired,
    time: timeShape.isRequired,
    roomName: PropTypes.string.isRequired,
    floor: PropTypes.number.isRequired,
    triggerModal: PropTypes.func.isRequired,
    resetValues: PropTypes.func.isRequired,
    resetRoom: PropTypes.func.isRequired
  }

  containerStyles = {
    textAlign: 'center'
  }

  paragraphStyles = {
    marginTop: '-10px'
  }

  headlineStyles = {
    marginBottom: '20px'
  }

  btnStyles = {
    marginTop: '10px'
  }

  handleSubmit = () => {
    const { triggerModal, resetValues } = this.props
    triggerModal(TRIGGER_SUCCESS_MODAL)
    resetValues()
  }

  componentWillUnmount () {
    this.props.resetRoom()
  }

  render() {
    const { 
      date: {
        startingMonth,
        startingDate
      },
      roomName,
      floor,
      time: {
        startingHours,
        startingMinutes,
        endingHours,
        endingMinutes
      },
      intl: { 
        formatMessage
      },
      monthsValue
    } = this.props

    const text = formatMessage({id: 'eventCreated.text'})
    const picAlt = formatMessage({id: 'eventCreated.picAlt'})
    const submitBtnText = formatMessage({id: 'eventCreated.submitBtnText'})
    const date = formatMessage({id: 'eventCreated.date'}, {
      startingDate,
      startingHours,
      startingMinutes,
      endingHours,
      endingMinutes,
      month: monthsValue[startingMonth]
      })
    const roomInfo = formatMessage({
      id: 'eventCreated.roomInfo'
    }, {roomName, floor})

    return (
      <DialogWindow
        text={text}
        picUrl={cheers}
        picAlt={picAlt}
        css={this.containerStyles}
      >
        <Paragraph css={this.paragraphStyles}>
          {date}
        </Paragraph>
          <Paragraph css={this.paragraphStyles}>{roomInfo}</Paragraph>

          <SubmitBtn
            text={submitBtnText}
            css={this.btnStyles}
            onClick={this.handleSubmit}
          />
      </DialogWindow>
    )
  }
}


const mapStateToProps = state => ({
  date: state.eventsReducer.currentEvent.date,
  monthsValue: state.dateReducer.monthsValue,
  time: state.eventsReducer.currentEvent.time,
  roomName: state.eventsReducer.currentEvent.room.name,
  floor: state.eventsReducer.currentEvent.room.floor
})

const mapDispatchToProps = {
  triggerModal,
  resetValues,
  resetRoom
}

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(EventCreated))


